import React from "react";

const SliderLeft = props => {
  const leftWidth = props.sliderLeftWidth + 40;
  return (
    <div
      className={"slider-range-background"}
      style={{
        width: `100%`,
        background: `linear-gradient(90deg, ${props.bandColors}`
      }}
      id={props.id}
    >
      <div
        style={{
          width: `${leftWidth}px`,
          display: "inline-block",
          height: "100%"
        }}
        id={props.sliderLeftId}
      />
      <div
        className="slider-range-right"
        id={props.sliderRightId}
        style={{
          width: `calc(100% - ${leftWidth}px)`,
          backgroundColor: "#ccc",
          display: "inline-block"
        }}
      ></div>
    </div>
  );
};

export default SliderLeft;
