export function generateId(prefix = "") {
  const letters = getRandomLetters(4);
  return prefix + letters + Math.floor(Date.now() / 1000);
}

function getRandomLetters(n) {
  const alpha = "abcdefghijklmnopqrstuvwxyz";
  let retval = "";
  for (let i = 0; i < n; i++) {
    retval += alpha.charAt(getRandomInt(0, 25));
  }
  return retval;
}

function getRandomInt(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

export function setPct() {
  return new Promise(resolve => {
    const pct = this.state.value / this.state.topRange;
    this.setState(
      {
        pct
      },
      () => {
        resolve();
      }
    );
  });
}

export function updatePct(x) {
  clearTimeout(this.timer);
  this.timer = setTimeout(() => {
    const percentage = pct(x, this.sliderContainerRect.width);
    return percentage;
  }, 25);
}

export function updateValues(x) {
  clearTimeout(this.timer);
  this.timer = setTimeout(() => {
    let percentage = pct(x, this.sliderContainerRect.width);
    if (percentage > 0.99) {
      percentage = 1;
    } else if (percentage < 0) {
      percentage = 0;
    }
    let min = this.props.bottomRange || 0;
    let newValue = parseInt(
      ((this.state.topRange - min) * percentage).toFixed(
        this.state.decimalPlaces
      )
    );
    const value = min + newValue;

    this.setState(
      {
        value: min + newValue
      },
      () => {
        if (this.props.onUpdate) {
          this.props.onUpdate(this.props.keyName, value);
        }
      }
    );
  }, 10);
}

export function pct(value, total) {
  return (value / total).toFixed(2);
}
