import React from "react";

export default class SliderValue extends React.Component {
  constructor(props) {
    super(props);
    this.previousX = null;
  }

  componentDidMount() {
    this.setLeft();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.sliderLeft !== this.props.sliderLeft) {
      this.setLeft();
    }
  }

  setLeft = () => {
    const target = document.querySelector(`#${this.props.id}`);
    target.style.left = `${this.props.sliderLeft}px`;
  };

  render() {
    return (
      <div className="sliderValue" id={this.props.id}>
        {Number(this.props.value).toLocaleString()}
      </div>
    );
  }
}
