import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import { About, Home } from "./containers";
import "./assets/css/App.scss";

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/about">
          <About />
        </Route>
        <Route path="*">
          <Home />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
