import React, { useEffect, useCallback, useState, useRef } from "react";

export default props => {
  const inputRef = useRef(null);
  let [value, setValue] = useState({});

  const format = useCallback(
    value => {
      let formatted;
      switch (props.format) {
        case "number":
          formatted = number(value);
          break;

        case "currency":
          formatted = currency(value);
          break;

        case "float":
          formatted = float(value);
          break;

        default:
          formatted = value;
          break;
      }
      setValue(formatted);
    },
    [props.format]
  );

  useEffect(() => {
    inputRef.current.value = format(props.defaultValue);
  }, [format, props.defaultValue]);

  const number = value => {
    return value.toLocaleString("en-US");
  };

  const float = value => {
    console.log(value);
    return parseFloat(value).toFixed(2);
  };

  const currency = value => {
    const reg = new RegExp("[^0-9|.]", "gi");
    let number = value.replace(reg, "");
    return Number(number).toLocaleString("en-US", {
      style: "currency",
      currency: "USD"
    });
  };

  return (
    <div className="flex-row">
      <label>{props.label}</label>
      <input
        type="text"
        className="text-input"
        name={props.formName}
        value={value}
        onChange={e => setValue(inputRef.current.value)}
        onBlur={() => format(value)}
        readOnly={props.readOnly}
        ref={inputRef}
      />
    </div>
  );
};
