import React from "react";
import Caret from "./Caret";

function withSection(WrappedComponent, title) {
  return class extends React.Component {
    state = { showCaret: false };
    constructor(props) {
      super(props);
      this.ref = React.createRef();
    }

    setCaret = () => {
      this.setState(prevState => ({
        showCaret: !prevState.showCaret
      }));
    };

    render() {
      return (
        <div
          className="inputs-row"
          ref={this.ref}
          onMouseOver={this.setCaret}
          onMouseOut={this.setCaret}
        >
          <Caret showCaret={this.state.showCaret} />
          <div className="inputs-row-body">
            <div className="inputs-row-title">{title}</div>
            <WrappedComponent />
          </div>
        </div>
      );
    }
  };
}

export default withSection;
