import React from "react";
import SliderValue from "./SliderValue";
import SliderBackground from "./SliderBackground";
import "./slider.scss";
import * as Lib from "./slider.lib";
import { PropTypes } from "prop-types";

export default class Slider extends React.PureComponent {
  state = {
    value: this.props.value,
    topRange: this.props.topRange,
    bottomRange: this.props.bottomRange,
    sliderRightWidth: 0,
    pct: 0,
    draggable: false,
    decimalPlaces: this.props.decimalPlaces || 0,
    bandColors: ""
  };

  constructor(props) {
    super(props);
    this.timer = null;
    this.sliderContainerRef = React.createRef();
    this.sliderRangeId = Lib.generateId("slider-range-");
    this.sliderValueId = Lib.generateId("slider-value-");
    this.sliderRightId = Lib.generateId("slider-right-");
    this.sliderLeftId = Lib.generateId("slider-left-");
  }

  componentDidMount() {
    this.init();
    this.props.onUpdate(this.props.keyName, this.props.value);
  }

  init = async () => {
    await Lib.setPct.call(this);
    this.initSlider();
  };

  initSlider = () => {
    return new Promise(resolve => {
      this.initSliderContainer();
      this.initSliderLeft();
      this.initValueSlider();
      this.initBarriers();
      this.initListeners();
      this.initBandColors();
      this.initBackgroundRange();
      this.setState({ sliderLeft: this.sliderLeft }, () => resolve());
    });
  };

  initSliderContainer = () => {
    this.sliderContainerElement = this.sliderContainerRef.current;
    this.sliderContainerRect = this.sliderContainerElement.getBoundingClientRect();
  };

  initValueSlider = () => {
    this.valueSliderElement = document.querySelector(`#${this.sliderValueId}`);
    this.valueSliderElement.onmousedown = this.dragStart;
    this.valueSliderRect = this.valueSliderElement.getBoundingClientRect();
  };

  initSliderLeft = () => {
    this.sliderLeft =
      Math.ceil((this.sliderContainerRect.width * this.state.pct) / 10) * 10;
    this.setState({
      sliderLeft: this.sliderLeft
    });
    //this.leftSliderElement = document.querySelector(`#${this.sliderRangeId}`);
  };

  initListeners = () => {
    window.addEventListener(
      "mouseup",
      e => {
        this.dragEnd();
      },
      false
    );
  };

  initBarriers = () => {
    this.rightBarrier =
      this.sliderContainerRect.width - this.valueSliderRect.width / 2;
    this.leftBarrier = 0 - this.valueSliderRect.width / 2;
  };

  initBandColors = () => {
    let colors = this.props.colors.split(",");
    if (colors.length === 1) {
      colors[1] = colors[0];
    }
    const bandColors = `${colors[0]} 0%, ${colors[0]} 10%, ${colors[1]} 75%, ${colors[1]} 100%`;
    this.setState({
      bandColors
    });
  };

  initBackgroundRange = () => {
    this.sliderRightElement = document.querySelector(`#${this.sliderRightId}`);
    this.sliderLeftElement = document.querySelector(`#${this.sliderLeftId}`);
  };

  dragStart = e => {
    this.draggable = true;
    this.valueSliderElement.onmousemove = this.dragging;
    this.valueSliderRect = this.valueSliderElement.getBoundingClientRect();
    this.pointOfContact = e.clientX - this.valueSliderRect.left;
    window.addEventListener("mousemove", e => {
      this.dragging(e);
    });
    this.setState({
      draggable: true
    });
  };

  dragEnd = () => {
    this.draggable = false;
    this.setState({
      draggable: false
    });
    window.removeEventListener(
      "mouseup",
      e => {
        this.dragStart(e);
      },
      false
    );
    window.removeEventListener(
      "mousemove",
      e => {
        this.dragStart(e);
      },
      false
    );
  };

  dragging = e => {
    if (!this.state.draggable) return;
    const elementX =
      e.clientX - this.sliderContainerRect.left - this.pointOfContact;
    Lib.updateValues.call(this, elementX);

    if (elementX < this.leftBarrier || elementX > this.rightBarrier) {
      return;
    }
    const width = elementX + 40;
    this.valueSliderElement.style.left = `${elementX}px`;
    this.sliderRightElement.style.width = `calc(100% - ${width}px`;
    this.sliderLeftElement.style.width = `${width}px`;
  };

  render() {
    return (
      <div className="slider-outer-container">
        <div className="slider-label">{this.props.label}</div>
        <div className="slider-container">
          <div className="slider-range-container" ref={this.sliderContainerRef}>
            <React.Fragment>
              <SliderBackground
                className="slider-range-background"
                sliderLeftWidth={this.state.sliderLeft}
                id={this.sliderRangeId}
                bandColors={this.state.bandColors}
                sliderLeftId={this.sliderLeftId}
                sliderRightId={this.sliderRightId}
              />

              <SliderValue
                value={this.state.value}
                sliderLeft={this.state.sliderLeft}
                updateValue={this.updateValue}
                id={this.sliderValueId}
              />
            </React.Fragment>
          </div>
        </div>
      </div>
    );
  }
}

Slider.propTypes = {
  colors: PropTypes.string
};

Slider.defaultProps = {
  colors: "lightsteelblue, steelblue"
};
